<template>
<v-container>
<v-row>
  <v-col cols="12" md="4">
    <div>
   <h3 class="text-center">Roles</h3>
    <v-card :disabled="Change" v-for="(item,index) in roles" :key="index" link  @click="SelectRol(item.descripcion)" class="pl-2 pa-2 mb-1 d-flex align-center justify-space-between">
       <div class="subtitle-1 font-weight-bold">{{item.descripcion}}</div>
      <v-card v-if="item.value"
      color="blue darken-4"
      elevation="1"
      height=30
      rounded
      width="30"
      ></v-card>
    </v-card>
     <div>
      <h4> Seleccionado: <span class="blue--text text--darken-4 font-weight-bold">{{UsuarioPendiente.nombre}}</span></h4>
      <h4> Rol: <span v-if="(UsuarioPendiente.nombre).trim()" class="blue--text text--darken-4 font-weight-bold">{{rolUsuarioPendiente}}</span></h4>
     </div>
     <v-btn v-if="(UsuarioPendiente.nombre).trim()" :disabled="btnGuardar" class="my-5" block dark color="blue darken-4" @click="ChangeRolUser">
              Guardar
     </v-btn>
      <v-alert v-if="registroRolHecho" dense class="text-center" type="success">
          Modificación Exitosa</v-alert>
     </div>
  </v-col>
 <v-col cols="12" md="6">
   <v-simple-table 
    dark
    fixed-header
   class="my-2" 
   height="400px"
   >
     <template v-slot:default>
        <thead>
          <tr>
          <th  :class="color_salud" class=" text-center subtitle-1 font-weight-bold white--text " style="border-top-left-radius:5px;border-bottom-left-radius:5px" >
            Nombres
          </th>
          <th :class="color_salud" class=" text-center subtitle-1 font-weight-bold white--text " style="border-top-right-radius:5px;border-bottom-right-radius:5px">
            Email
          </th>
          <th :class="color_salud" class=" text-center subtitle-1 font-weight-bold white--text " style="border-top-right-radius:5px;border-bottom-right-radius:5px">
            
          </th>
          </tr>
        </thead>
            <tbody >
              <tr v-for="item in Usuarios" :key="item.idusuario"  class="font-weight-bold "   >
                <td class="text-center">{{item.usuario}}</td>
                <td class="text-center blue-grey--text text-darken-4 ">{{item.email}}</td>
                <td class="text-center">
                  <v-btn  @click="SelectUsuario(item.idusuario,item.usuario,item.rol)"  elevation="3" icon outlined small class="ma-1">        
                <v-icon>edit</v-icon>
                  </v-btn>  
                  <v-btn
                    elevation="3"
                    icon
                    outlined
                    small
                    class="ma-1"
                    @click="ShowDialogDeleteUsuario(item.usuario, item.rol,item.idusuario )" >
                    <v-icon>delete</v-icon>
                  </v-btn>
                </td>
              </tr>
            </tbody>
     </template>
   </v-simple-table>
  
 </v-col>
</v-row>
<v-dialog v-model="dialogDeleteUsuario" max-width="300" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            Eliminar a
            <span class="text-center blue--text text--darken-4  ma-2">
              {{ dataDialogDeleteUsuario.usuario }}
            </span>
          </div>
          <div class="font-weight-bold">
            con rol de
            <span class="text-center blue--text text--darken-4  ma-2">
              {{ dataDialogDeleteUsuario.rol }}
            </span>
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout mx-2 :justify-space-between="true">
            <v-btn
              @click="Eliminar(dataDialogDeleteUsuario.id)"
              rounded
              dark
              
              >Si</v-btn>
            <v-btn @click="NotShowDialogDeleteUsuario" color="blue darken-4 " rounded dark>No</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
</v-container>
</template>
<script>
import {mapState} from 'vuex'
  export default {
    name:'forms',
    data: () => ({
      dialogDeleteUsuario:false,
       dataDialogDeleteUsuario: {
        id: null,
        rol: null,
        usuario: null,
      },
      color_salud:'blue darken-3',
      color_text_salud:'blue--text text-darken-3',
      registroRolHecho:false,
      btnGuardar:true,
      UsuarioPendiente:{id:'',nombre:''},
      rolUsuarioPendiente:'',
      Change:false,
      Usuarios:[],
      roles:[],
      show1:false,
      selectedItem: 1,
      items: [
        { text: 'Real-Time', icon: 'mdi-clock' },
        { text: 'Audience', icon: 'mdi-account' },
        { text: 'Conversions', icon: 'mdi-flag' },
      ],
    }),
    computed:{
        ...mapState(['token'])
    },
    mounted(){
      this.listarUsuario()
      this.listarRol()
    },
    methods: {
      listarUsuario(){
          this.axios.get('/listarUsuario')
          .then(res=>{
            this.Usuarios=res.data
          })
          .catch(err=>console.log(err))
      },
       listarRol(){
          this.axios.get('/listarRol')
          .then(res=>{
            res.data.map(e=>{
              this.roles.push({descripcion:e.descripcion,value:false})
            })
          })
          .catch(err=>console.log(err))
      },
      SelectRol(item){
        this.btnGuardar=false;
        this.Change=true;
        this.roles.map( ele =>{ 
          if  (item===ele.descripcion )
              { 
                this.rolUsuarioPendiente=item;
                ele.value=true 
                }
          else
              { ele.value=false}
              }
        )        
        this.Change=false;
    },
    SelectUsuario(idusuario,usu,rol){
      this.UsuarioPendiente.id=idusuario;
      this.UsuarioPendiente.nombre=usu;
      this.SelectRol(rol);
    },
    ChangeRolUser(){
      var headers = {
        "Content-Type": "application/json",
        token: this.token,
      };
        if(this.UsuarioPendiente.id>0  && this.rolUsuarioPendiente){
          this.axios.post("/saveRoles",{
            idusuario:this.UsuarioPendiente.id,
            rol:this.rolUsuarioPendiente
          },{headers})
          .then(res =>{
              if(res.data.mensaje==='Registro Exitoso'){
              this.listarUsuario()
              this.ShowMessageRegisterRol();
              }
          }).catch(err=> console.log(err.response.data))
        }
    },
     ShowMessageRegisterRol(){
     this.registroRolHecho=true;
      setTimeout(()=>{this.registroRolHecho=false}, 3000);
    },
     NotShowDialogDeleteUsuario() {
      this.dialogDeleteUsuario = false;
      this.dataDialogDeleteUsuario.rol = null;
      this.dataDialogDeleteUsuario.id = null;
       this.dataDialogDeleteUsuario.usuario = null;
    },
    ShowDialogDeleteUsuario(usuario,rol,id){     
      this.dataDialogDeleteUsuario.rol = rol;
      this.dataDialogDeleteUsuario.id = id;
      this.dataDialogDeleteUsuario.usuario = usuario;
      this.dialogDeleteUsuario = true;
    },
    Eliminar(id) {
      
     var headers = {
        "Content-Type": "application/json",
        token: this.token,
      };

          this.axios.post("/deleteUser",{
            idUsuario:id,
          },{headers})
          .then(res =>{
              console.log("Eliminando.." + id);
              this.dialogDeleteUsuario = false;
              this.listarUsuario()
          }).catch(err=> console.log(err.response.data))
          
    },
    },


  }

</script>